export default {
	data() {
		return {
			dis:'b',disX:'', disY:'', L:'', T:'', starX:'', starY:'',
		}
	},props:{
		text:{
			type:Object,default(){return{ulTxt:'≡',liTxt1:'V',liTxt2:'>',liTxt3:'↑'}
				
				}
			},ulClass:{type:String,default:'ulClass'},
			listy1:{type:Object,default(){
				return{
					
				}
			}},listy2:{type:Object,default(){
				return{
					
				}
			}},listy3:{type:Object,default(){
				return{
					
				}
			}}
			
		}
		
	,
	onLoad() {

	},
	methods: {
		licli:function(e){
			console.log(1)
			this.$emit('bbclick',this.text)
		}
	,show:function(){
			if(this.dis==='b'){
				this.dis='s';
			}
			else if(this.dis==='s'){
				this.dis='b';
			}
		},
		touch:function(e){
			
			this.disX = e.touches[0].clientX - e.currentTarget.offsetLeft;
			this.disY = e.touches[0].clientY - e.currentTarget.offsetTop;
			this.starX = e.touches[0].clientX;
			this.starY = e.touches[0].clientY;
			
		}
		,
		move:function(e){
			this.L = e.touches[0].clientX - this.disX;
			this.T = e.touches[0].clientY - this.disY;
			
			if (this.L < 0) {
			this.L = 0;
			} else if (this.L > document.documentElement.clientWidth - e.currentTarget.offsetWidth) {
			this.L = document.documentElement.clientWidth - e.currentTarget.offsetWidth;
			}//documentElement代表html页面这个元素
			if (this.T < 0) {
			this.T = 0;
			} else if (this.T > document.documentElement.clientHeight - e.currentTarget.offsetHeight) {
			this.T = document.documentElement.clientHeight - e.currentTarget.offsetHeight;
			}
			
			
			
			
		}
}}
